import { mapGetters } from "vuex";
import { db } from "~/plugins/firebase/firebaseConfig.js";
import { mapActions } from "vuex";

import { Validator } from 'vee-validate';
const dict = {
  custom: {
    firstName: {
        required: () => 'Veuillez renseigner votre prénom'
    },
    lastName: {
        required: () => 'Veuillez renseigner votre nom'
    },
    email: {
      required: () => 'Veuillez renseigner votre adresse email',
      email: () => 'Merci d\'entrer une adresse email valide'
    }
  }
};
Validator.localize('fr', dict);
export default {
  data() {
    return {
      enableValidate:true,
      dataForm: {
        email: "",
        firstname: "",
        lastname: "",
        newsletter: true,
        phone: "",
        id: "",
        status: "Non traité",
        addDate: new Date(),
      },
    };
  },
  mounted() {
    this.dataForm.email = JSON.parse(JSON.stringify(this.getProps)).email;
  },
  methods: {
    ...mapActions({
      close: "popup/hide",
    }),
    addLead() {
    },
  },
  computed: {
    ...mapGetters({
      getProps: "popup/getProps",
    }),
  },
};
